      <Input label="æ‰‹æœºå·" type="tel" maxlength="11" on:setInput={getPhone} />
      {#if phoneType === 'pwd'}
        <Input label="å¯†ç " type="password" maxlength="40" bind:password_phone on:setInput={getpassword_phone} />
      {/if}
      {#if phoneType === 'code'}
        <Input
          label="éªŒè¯ç "
