            }}
          >
            çŸ­ä¿¡éªŒè¯
          </span>{/if}
        {#if phoneType === 'code'}
          <span
            on:click={() => {
              phoneType = 'pwd';
            }}
          >
            å¯†ç éªŒè¯
          </span>
        {/if}
