      <div class="btn">
        <Button on:BtnClick={getClickPhone} type="primary">ç™»å½•</Button>
      </div>
      {#if phoneType === 'pwd'}
        <div class="tip">{tiptext}</div>
      {/if}
      <div class="type-switch">
        <span on:click={qrLoginFun}> äºŒç»´ç ç™»å½• </span>&nbsp;ï½œ&nbsp;
        <span
          on:click={() => {
            type = 'email';
            clearInterval(timerLogin);
          }}
        >
          é‚®ç®±ç™»å½•
        </span>
      </div>
    </div>
  {/if}

  {#if type === 'email'}
    <div class="email">
      <Input label="é‚®ç®±" maxlength={30} type="text" on:setInput={getEmail} />
      <Input label="å¯†ç " type="password" maxlength="40" bind:password_email on:setInput={getpassword_email} />
      <div class="btn">
        <Button on:BtnClick={getClickEmail} type="primary">ç™»å½•</Button>
